/**
 * Styles for nav-overlay-button
 */

$tablet-breakpoint: 1024px;
$mobile-breakpoint: 767px;

$highlight-color: #a7ad8b;
// $overlay-color: #a7ad8bef;
$overlay-color: #000000e5;

button.wp-block-navigation__responsive-container-open {
    display: none;
    margin: 0px;
}

.wp-block-create-block-navigation-overlay {
    margin-left: -4rem;
    width: 96px;
    height: 32px;

    margin-left: -32px;
    filter: drop-shadow(black 0px 0px 100px);

    transition: transform 0.2s ease-in-out;

    &:hover {
        transform: scale(1.03);
    }

    .nav-overlay-button {
        width: 96px;
        height: 32px;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: nowrap;
        gap: 13px;

        cursor: pointer;
        background-color: $highlight-color;
        color: white;

        .nav-overlay-button-circle {
            width: 12px;
            height: 12px;
            background-color: white;
        }
    }

}



.wp-block-navigation__responsive-container {
    background-color: $overlay-color !important;
    backdrop-filter: blur(5px);

    color: white !important;

    font-family: 'Avenir Next', sans-serif;
    font-size: 2.8rem;
    letter-spacing: 3%;

    .wp-block-navigation__responsive-close {
        max-width: 96rem !important;
    }

    .wp-block-search__button-inside,
    .wp-block-search__button-outside {
        position: absolute;
        right: 0;
        top: 11rem;

        @media (max-width: $mobile-breakpoint) {
            top: 9.5rem;
        }

        .wp-block-search__inside-wrapper {
            width: 100% !important;
            max-width: 48rem;

            height: 4.8rem;

            margin-left: auto;

            border: none;
            background-color: #ffffff11;

            .wp-block-search__input {
                background: transparent !important;
                font-size: 2.4rem !important;
                padding-left: 1.2rem;
                color: white !important;

                .placeholder {
                    color: white;
                    opacity: 0.6;
                }
            }

            .wp-block-search__button {
                background-color: transparent !important;

                svg {
                    color: white;
                    transform: scale(2.4) translate(-0.24rem);
                }
            }
        }
    }

    @media (max-width: $mobile-breakpoint) {
        .wp-block-search {
            width: 100% !important;
        }
    }

    .wp-block-navigation__responsive-container-content {
        gap: 1rem !important;
        margin-left: 2.8rem;

        @media (max-width: $mobile-breakpoint) {
            margin-left: 0rem;
        }

        &:before {
            content: 'HARVESTWORKS';
            letter-spacing: 33%;
            font-family: "Avenir Next Condensed";
            font-size: 3.5rem;
            font-style: normal;
            font-weight: 600;
            text-align: center;
            width: 100%;
            margin-bottom: 1rem;

            @media (max-width: $mobile-breakpoint) {
                font-size: 2.5rem;
                margin-bottom: 8rem;
            }
        }

        .wp-block-navigation__container {}

    }



    .wp-block-navigation__submenu-container {
        padding-top: 12px;
        padding-bottom: 12px;

    }
}